Object.isPlainObject = function (obj) {
  var proto
  var Ctor
  if (!obj || toString.call(obj) !== '[object Object]') {
    return false
  }

  proto = Object.getPrototypeOf(obj)

  // `Object.createnull`true
  if (!proto) {
    return true
  }

  // Object
  Ctor = {}.hasOwnProperty.call(proto, 'constructor') && proto.constructor
  return typeof Ctor === 'function' && {}.hasOwnProperty.toString.call(Ctor) === {}.hasOwnProperty.toString.call(Object)
}

/* -------------------
  Array
  ------------------- */
Object.clone = function (obj) {
  var copy
  if (typeof obj === 'object') {
    if (obj === null) {
      copy = null
    } else {
      if (obj instanceof Array) {
        copy = []
        for (var i = 0; i < obj.length; i++) {
          copy.push(this.clone(obj[i]))
        }
      } else {
        copy = {}
        for (var j in obj) {
          copy[j] = this.clone(obj[j])
        }
      }
    }
  } else {
    copy = obj
  }
  return copy
}

/*
  * get,Jsonparams
  * : obj:; splitter:dot | bracket, isNotEnCode:encodeURIComponent
  * : splitterdot,obj.key=value&obj.key=value; bracket,obj[key]=value&obj[key]=value
  * */
Object.params = function (obj, splitter, isNotEnCode) {
  if (!Object.isPlainObject(obj)) return obj
  if (obj instanceof Object && obj.length > 0) return ''
  // {jsonReq:[{0:'0', 1:'1'}]}jsonReq=[{0:'0', 1:'1'}]
  if (splitter === 'dot') {
    var arr = []
    for (var n in obj) {
      arr.push(n + '=' + JSON.stringify(obj[n]))
    }
    return arr.join('&')
  }
  // {jsonReq:[{0:'0', 1:'1'}]}jsonReq.0=0&jsonReq.1=1(Json)
  var result = ''
  function buildParams(obj, prevKey) {
    for (var key in obj) {
      if (obj[key] instanceof Object) {
        var prefix = prevKey ? prevKey + '.' + key : key
        buildParams(obj[key], prefix)
      } else {
        if (prevKey) {
          // result += '&' + prevKey + '.' + key + '=' + obj[key]
          if (splitter !== 'bracket') result += '&' + prevKey + '.' + key + '=' + (isNotEnCode ? obj[key] : encodeURIComponent(obj[key]))
          if (splitter === 'bracket') result += '&' + prevKey + '[' + key + ']=' + (isNotEnCode ? obj[key] : encodeURIComponent(obj[key]))
        } else {
          // result += '&' + key + '=' + obj[key]
          result += '&' + key + '=' + (isNotEnCode ? obj[key] : encodeURIComponent(obj[key]))
        }
      }
    }
    return result
  }
  buildParams(obj)
  // result
  if (result) {
    result = result.slice(1)
  }
  return result
}
