// BaiduMap 
var BaiduMap = function (id, params) {
  if (!window._seeds_lang) window._seeds_lang = {} // 
  if (!document.querySelector('#' + id)) {
    console.log('SeedsUI ErrorBaiduMapDOMid')
    return
  }
  /* --------------------
  Model
  -------------------- */
  var defaults = {
    styleOptions: {
      strokeColor: '#0C8EFF', //
      strokeWeight: 1, //
      strokeOpacity: 0.8, //0 - 1
      strokeStyle: 'solid', //soliddashed
      fillColor: '#0C8EFF', //
      fillOpacity: 0.6 //0 - 1
    },
    labelStyleOptions: {
      color : 'red',
      fontSize : '12px',
      height : '20px',
      lineHeight : '20px',
      fontFamily: ''
    }
    /*  callback
    onSubmit:function(selected)
    */
  }
  params = params || {}
  for (var def in defaults) {
    if (params[def] === undefined) {
      params[def] = defaults[def]
    }
  }

  // BaiduMap
  var s = this

  // Params
  s.params = params
  
  s.map = null
  s.drawingManager = null
  const {
    BMap, BMapLib,
    BMAP_ANCHOR_BOTTOM_LEFT, BMAP_ANCHOR_BOTTOM_RIGHT, BMAP_ANCHOR_TOP_RIGHT,
    BMAP_DRAWING_POLYGON, BMAP_NAVIGATION_CONTROL_ZOOM
  } = window

  // 
  s.createMap = function () {
    s.map = new BMap.Map('map')
    s.map.centerAndZoom(new BMap.Point(116.404, 39.915), 12) // ,
    s.map.setCurrentCity('') //  
    s.map.enableScrollWheelZoom(true) //
  }
  s.createMap()

  // 
  s.createDrawingManager = function () {
    s.drawingManager = new BMapLib.DrawingManager(s.map, {
      isOpen: false, // 
      drawingToolOptions: {
        anchor: BMAP_ANCHOR_TOP_RIGHT, // 
        offset: new BMap.Size(5, 5), // 
      },
      circleOptions: s.params.styleOptions, // 
      polylineOptions: s.params.styleOptions, // 
      polygonOptions: s.params.styleOptions, // 
      rectangleOptions: s.params.styleOptions // 
    })
  }
  s.createDrawingManager()

  /* --------------------
  
  -------------------- */
  
  /* --------------------
  Method
  -------------------- */
  // points, [[lng, lat], [lng, lat]][{lng: '', lat: ''}]
  s.formatPoints = function (points) {
    if (!points || !Array.isArray(points)) return []
    if (JSON.stringify(points).indexOf('lng') !== -1) return points
    if (!Array.isArray(points[0]) || !points[0][0] || !points[0][1]) return []
    return points.map(function (point) {
      var lng = point[0]
      var lat = point[1]
      if (point[0] < point[1]) {
        lng = point[1]
        lat = point[0]
      }
      return {
        lng,lat
      }
    })
  }
  // 
  s.showScale = function () {
    s.map.addControl(new BMap.ScaleControl({anchor: BMAP_ANCHOR_BOTTOM_LEFT}))
  }
  // 
  s.showNavigation = function () {
    s.map.addControl(new BMap.NavigationControl({anchor: BMAP_ANCHOR_BOTTOM_RIGHT, type: BMAP_NAVIGATION_CONTROL_ZOOM}))
  }
  // 
  // DrawingType5: 
  // BMAP_DRAWING_MARKER  
  // BMAP_DRAWING_CIRCLE  
  // BMAP_DRAWING_POLYLINE  
  // BMAP_DRAWING_POLYGON  
  // BMAP_DRAWING_RECTANGLE 
  // circlecomplete(overlay) {Circle} 
  // markercomplete(overlay) {Marker} 
  // overlaycomplete(e) {Event Object} 
  // polygoncomplete(overlay) {Polygon} 
  // polylinecomplete(overlay) {Polyline} 
  // rectanglecomplete(overlay) {Polygon} 
  s.enableManualDraw = function (type) {
    if (s.drawingManager) {
      s.drawingManager.open()
      s.drawingManager.setDrawingMode(type || BMAP_DRAWING_POLYGON) // 
    }
  }
  // 
  s.autoViewport = function () {
    var overlays = s.map.getOverlays()
    var points = []
    for(var i = 0; i < overlays.length; i++) {
      var overlay = overlays[i];
      if(overlay instanceof BMap.Polygon) {
        points = points.concat(overlay.getPath())
      }
    }
    s.map.setViewport(points)
  }
  // points:[[lng, lat], [lng, lat]]
  s.pointsToPolygon = function (argPoints) {
    var points = s.formatPoints(argPoints);
    var ps = []
    for (var point of points) {
      ps.push(new BMap.Point(point.lng, point.lat))
    }
    return new BMap.Polygon(ps)
  }
  // points:[[ [lng, lat], [lng, lat] ], [ [lng, lat], [lng, lat] ]]
  s.pointsToPolygons = function (argPointss) {
    let polygons = []
    for (let i = 0; i < argPointss.length; i++) {
      let polygon = s.pointsToPolygon(argPointss[i])
      polygons.push(polygon)
    }
    return polygons
  }
  // 
  s.drawBoundary = function (options = {}) { // {area: '', styleOptions: {}, onSuccess: func(), onError: func()}
    var boundary = new BMap.Boundary()
    if (!options.area) {
      console.warn(`${window._seeds_lang['hint_pass_in_parameters'] || ''}area, ${window._seeds_lang['hint_for_example_address'] || ''}`)
      options.onError && options.onError({
        errMsg: `${window._seeds_lang['hint_pass_in_parameters'] || ''}area, ${window._seeds_lang['hint_for_example_address'] || ''}`
      })
      return
    }
    boundary.get(options.area, function (res) { // 
      var count = res.boundaries.length // 
      if (count === 0) {
        console.warn(`${window._seeds_lang['hint_pass_in_correct_parameters'] || ''}area`)
        options.onError && options.onError({
          errMsg: `${window._seeds_lang['hint_pass_in_correct_parameters'] || ''}area`
        })
        return
      }
      var polygons = []
      var polygonsPath = []
      for (var i = 0; i < count; i++) {
        polygons[i] = new BMap.Polygon(res.boundaries[i], options.styleOptions || s.params.styleOptions)
        s.map.addOverlay(polygons[i]) // 
        polygonsPath = polygonsPath.concat(polygons[i].getPath())
      }
      // s.map.setViewport(pointArray) //
      options.onSuccess && options.onSuccess({...res, polygons, polygonsPath})
    })
    return boundary
  }
  // 
  s.drawPolygon = function (options = {}){ // {polygon: Object, points: [], styleOptions: {}}
    var polygon = null
    if (options.polygon && Object.keys(options.polygon) && Object.keys(options.polygon).length) {
      polygon = options.polygon
      if (!options.styleOptions) options.styleOptions = {}
      // CSS
      polygon.setStrokeColor(options.styleOptions.strokeColor || s.params.styleOptions.strokeColor)
      // 1
      polygon.setStrokeWeight(options.styleOptions.strokeWeight || s.params.styleOptions.strokeWeight)
      // 0 - 1
      polygon.setStrokeOpacity(options.styleOptions.strokeOpacity || s.params.styleOptions.strokeOpacity)
      // soliddashed
      polygon.setStrokeStyle(options.styleOptions.strokeStyle || s.params.styleOptions.strokeStyle)
      // RGBRGBA
      polygon.setFillColor(options.styleOptions.fillColor || s.params.styleOptions.fillColor)
      // ,opacity0~1
      polygon.setFillOpacity(options.styleOptions.fillOpacity || s.params.styleOptions.fillOpacity)
    } else if (Array.isArray(options.points) && options.points.length) {
      polygon = new BMap.Polygon(s.formatPoints(options.points), options.styleOptions || s.params.styleOptions)
    }
    if (polygon) {
      s.map.addOverlay(polygon) // 
      options.onSuccess && options.onSuccess(polygon)
    } else {
      console.warn(`drawPolygon: ${window._seeds_lang['hint_pass_in_parameters'] || ''}{polygon: {}}${window._seeds_lang['or'] || ''}{points: []}`)
      options.onError && options.onError({
        errMsg: `drawPolygon: ${window._seeds_lang['hint_pass_in_parameters'] || ''}{polygon: {}}${window._seeds_lang['or'] || ''}{points: []}`
      })
    }
    return polygon
  }
  // 
  s.drawPolygons = function (options = []){ // [{polygon: Object, points: [], styleOptions: {}}]
    let polygons = []
    for (let option of options) {
      polygons.push(s.drawPolygon(option))
    }
    return polygons
  }
  // Label
  s.drawLabel = function (options = {}){ // {point: {}, styleOptions: {}}
    if (!options.point) {
      console.warn(`drawLabel: ${window._seeds_lang['hint_pass_in_parameters'] || ''}{point: }`)
      options.onError && options.onError({
        errMsg: `drawLabel: ${window._seeds_lang['hint_pass_in_parameters'] || ''}{point: }`
      })
      return
    }
    var opts = {
      position: options.point, // 
      offset: new BMap.Size(0, 0) // 
    }
    var label = new BMap.Label(JSON.stringify(options.point), opts) // 
    label.setStyle(options.styleOptions || s.params.labelStyleOptions);
    s.map.addOverlay(label)
    options.onSuccess && options.onSuccess(label)
    return label
  }
  // 
  s.drawMarker = function (options = {}){ // {point: {lng: ,lat: }, styleOptions: {}}
    if (!options.point) {
      console.warn(`drawMarker: ${window._seeds_lang['hint_pass_in_parameters'] || ''}{point: }`)
      options.onError && options.onError({
        errMsg: `drawMarker: ${window._seeds_lang['hint_pass_in_parameters'] || ''}{point: }`
      })
      return
    }
    if (!options.point.lng || !options.point.lat) {
      console.warn(`drawMarker: ${window._seeds_lang['hint_pass_in_correct_parameters'] || ''}{point: {lng: ,lat: }}`)
      options.onError && options.onError({
        errMsg: `drawMarker: ${window._seeds_lang['hint_pass_in_correct_parameters'] || ''}{point: {lng: ,lat: }}`
      })
      return
    }
    var point = new BMap.Point(options.point.lng, options.point.lat)
    var marker = new BMap.Marker(point,
      {
        icon: new BMap.Icon(
          options.icon || 'data:image/png;base64,',
          new BMap.Size(16,24),
          {
            imageSize: new BMap.Size(16, 24) // 
          }
        )
      },
      {
        offset: new BMap.Size(8, 12)
      }
    )
    s.map.addOverlay(marker)
    options.onSuccess && options.onSuccess(marker)
    return marker
  }
  // 
  s.addContextMenu = function (overlay, options = {}){ // options: {menus: [{text: '', handler: func()}]}
    if (!overlay) {
      console.warn(`addContextMenu: ${window._seeds_lang['hint_pass_in_parameters'] || ''}overlay`)
      options.onError && options.onError({
        errMsg: `addContextMenu: ${window._seeds_lang['hint_pass_in_parameters'] || ''}overlay`
      })
      return
    }
    if (!options.menus || !Array.isArray(options.menus) || !options.menus[0] || !options.menus[0].text) {
      console.warn(`addContextMenu: ${window._seeds_lang['hint_pass_in_parameters'] || ''}{menus: [{text: "", handler: func()}]}`)
      options.onError && options.onError({
        errMsg: `addContextMenu: ${window._seeds_lang['hint_pass_in_parameters'] || ''}{menus: [{text: "", handler: func()}]}`
      })
      return
    }
    var markerMenu = new BMap.ContextMenu()
    for (let [index, opt] of options.menus.entries()) {
      markerMenu.addItem(new BMap.MenuItem(opt.text || (window._seeds_lang['menu'] || ''), function () {
        opt.handler(opt, index)
      }))
    }
    overlay.addContextMenu(markerMenu)
    return markerMenu
  }
}

export default BaiduMap



