// EditUtil 
var EditUtil = {
  // 
  selection: function () {
    return document.getSelection()
  },
  // 
  getTxtCusorPos: function (txt) {
    var cusorPos = -1
    // ie
    if (txt.selectionStart) {// IE
      cusorPos = txt.selectionStart
      return cusorPos
    }
    // ie
    if (document.selection && document.selection.createRange) {
      var range = document.selection.createRange()
      range.moveStart('character', -txt.value.length)
      cusorPos = range.text.length
      return cusorPos
    }
  },
  // 
  getDivCusorPos: function () {
    var cusorPos = 0//  
    // ie
    if (window.getSelection) {
      var selection = window.getSelection()
      // console.log(selection.anchorNode) // 
      // console.log(selection.focusNode) // 
      // console.log(selection.focusOffset) // 
      // console.log(selection.isCollapsed) // 
      // console.log(selection.rangeCount) // rangerange(Ctrl)

      // 
      cusorPos = selection.anchorOffset
      return cusorPos
    }
    // ie
    if (document.selection && document.selection.createRange) {
      var range = document.selection.createRange()
      var srcele = range.parentElement()
      var copy = document.body.createTextRange()
      copy.moveToElementText(srcele)
      for (cusorPos = 0; copy.compareEndPoints('StartToStart', range) < 0; cusorPos++) {
        copy.moveStart('character', 1)
      }
      return cusorPos
    }
  },
  // 
  selectionPos: function (classname) {
    var selection = window.getSelection()
    var cursorOffset = 0
    document.onselectionchange = function (e) {
      if (e.target.activeElement.className === classname) {
        cursorOffset = selection.anchorOffset
      }
    }
    return cursorOffset
  },
  /* --------------------------
  
  @method isenable
  @param commandName (backcolor)
  @return boolean
  -------------------------- */
  isenable: function (commandName) {
    return document.queryCommandEnabled(commandName)
  },
  backgroundcolor: function (color) {
    document.execCommand('backcolor', false, color)
  },
  bold: function () {
    document.execCommand('bold', false, null)
  },
  italic: function () {
    document.execCommand('italic', false, null)
  },
  underline: function () {
    document.execCommand('underline', false, null)
  },
  copy: function () {
    document.execCommand('copy', false, null)
  },
  selectall: function () {
    document.execCommand('selectall', false, null)
  },
  cut: function () {
    document.execCommand('cut', false, null)
  },
  paste: function () {
    document.execCommand('paste', false, null)
  },
  del: function () {
    document.execCommand('delete', false, null)
  },
  link: function (url) {
    document.execCommand('createlink', false, url)
  },
  unlink: function () {
    document.execCommand('unlink', false, null)
  },
  fontname: function (fontName) {
    document.execCommand('fontname', false, fontName)
  },
  fontsize: function (fontSize) {
    if (fontSize) {
      document.execCommand('fontsize', false, fontSize)
      return
    }
    return document.queryCommandValue('fontsize')
  },
  fontcolor: function (fontColor) {
    document.execCommand('forecolor', false, fontColor)
  },
  format: function (tag) {
    document.execCommand('formatblock', false, tag)
  },
  unformat: function () {
    document.execCommand('removeformat', false, null)
  },
  indent: function () {
    document.execCommand('indent', false, null)
  },
  outdent: function () {
    document.execCommand('outdent', false, null)
  },
  hr: function () {
    document.execCommand('inserthorzizontalrule', false, null)
  },
  img: function (url) {
    document.execCommand('insertimage', false, url)
  },
  ol: function () {
    document.execCommand('insertorderedlist', false, null)
  },
  ul: function () {
    document.execCommand('insertunorderedlist', false, null)
  },
  p: function () {
    document.execCommand('insertparagraph', false, null)
  },
  center: function () {
    document.execCommand('justifycenter', false, null)
  },
  left: function () {
    document.execCommand('justifyleft', false, null)
  },
  right: function () {
    document.execCommand('justifyright', false, null)
  },
  // 
  setCaretPosition: function (elem, caretPos) {
    if (elem !== null) {
      if (elem.createTextRange) {
        var range = elem.createTextRange()
        range.move('character', caretPos)
        range.select()
      } else {
        if (elem.selectionStart) {
          elem.focus()
          elem.setSelectionRange(caretPos, caretPos)
        } else {
          elem.focus()
        }
      }
    }
  },
  isEnter: function () {
    // 
    window.addEventListener('keydown', function (e) {
      var keynum = e.which || e.keyCode
      if (keynum === '13') {
        return true
      }
      return false
    }, false)
  }
}

export default EditUtil



