// Counter 
var Counter = function (counter, params) {
  /* ----------------------
  Model
  ---------------------- */
  var defaults = {
    fromAttr: 'data-from',
    toAttr: 'data-to',
    durationAttr: 'data-duration',
    suffixAttr: 'data-suffix', // 
    maxMilliSec: 50, // 50
    maxCountSec: 20 // 20
  }
  params = params || {}
  for (var def in defaults) {
    if (params[def] === undefined) {
      params[def] = defaults[def]
    }
  }
  // Counter
  var s = this

  // Params
  s.params = params

  // Counter
  s.counter = typeof counter === 'string' ? document.querySelector(counter) : counter

  // From()
  s.from = s.counter.getAttribute(s.params.fromAttr) ? s.counter.getAttribute(s.params.fromAttr) : 0
  // To()
  s.to = s.counter.getAttribute(s.params.toAttr) ? s.counter.getAttribute(s.params.toAttr) : 0
  // suffix()
  s.suffix = s.counter.getAttribute(s.params.suffixAttr) ? s.counter.getAttribute(s.params.suffixAttr) : ''
  // Current()
  s.current = s.from
  // Duration()
  s.duration = s.counter.getAttribute(s.params.durationAttr) ? s.counter.getAttribute(s.params.durationAttr) : 5000

  // Diff()
  s.diff = s.to - s.from

  // 
  var secNum = Math.round(s.diff / (s.duration / 1000))
  // 
  s.step = 1
  // /
  s.milliSec = Math.round(s.params.maxMilliSec)
  if (secNum > s.params.maxCountSec) { // 
    s.step = Math.round(secNum / s.params.maxCountSec)
    s.milliSec = s.params.maxMilliSec // 50
  } else {
    s.step = secNum
    s.milliSec = 1000 / secNum // 1
  }

  // console.log(''+s.from+''+s.to+''+s.duration/1000+''+secNum+''+s.milliSec+''+Math.round(1000/s.milliSec)+''+s.step+'')

  // Interval
  s.interval = null
  /* ----------------------
  Method
  ---------------------- */
  s.play = function () {
    if (s.diff < 0 || isNaN(s.from) || isNaN(s.to)) {
      console.error('SeedsUI Counter')
      return
    }
    s.interval = window.setInterval(function () {
      s.current = parseInt(s.current, 10) + parseInt(s.step, 10)
      s.counter.innerHTML = s.current + s.suffix
      if (s.current >= s.to) {
        s.counter.innerHTML = s.to + s.suffix
        clearInterval(s.interval)
      }
    }, s.milliSec)
  }
}
var Counters = function (params) {
  params = params || {}
  var counterClass = 'counter'
  var s = this
  s.counters = []
  s.update = function () {
    var elements = document.querySelectorAll('.' + counterClass)
    for (var i = 0; i < elements.length; i++) {
      s.counters[i] = new Counter(elements[i], s.params)
    }
  }
  s.update()
  s.play = function () {
    for (var i = 0; i < s.counters.length; i++) {
      s.counters[i].play()
    }
  }
}

export {Counter, Counters}



