// Clipboard 

if (!window._seeds_lang) window._seeds_lang = {} // 

var Clipboard = {
  // 
  selectContent: function (element) {
    // 
    var rangeToSelect = document.createRange();
    rangeToSelect.selectNodeContents(element);

    // 
    var selection = window.getSelection();
    selection.removeAllRanges();
    selection.addRange(rangeToSelect);
  },
  // execCommand
  createElementForExecCommand: function (textToClipboard) {
    var forExecElement = document.createElement('div');
    // 
    forExecElement.style.position = 'absolute';
    forExecElement.style.left = '-10000px';
    forExecElement.style.top = '-10000px';
    // 
    forExecElement.textContent = textToClipboard;
    document.body.appendChild(forExecElement);
    // contentEditableFirefoxexecCommand
    forExecElement.contentEditable = true;

    return forExecElement;
  },
  // 
  copy: function (input, params = {}) {
    var textToClipboard = input;
  
    var success = true;
    if (window.clipboardData) { // Internet Explorer
      window.clipboardData.setData('Text', textToClipboard);
    } else {
      // execCommand
      var forExecElement = this.createElementForExecCommand(textToClipboard);
  
      //  ('copy'execCommand)
      this.selectContent(forExecElement);
  
      // UniversalXPConnectFirefox
      try {
        if (window.netscape && window.netscape.security) {
          window.netscape.security.PrivilegeManager.enablePrivilege('UniversalXPConnect');
        }
        // 
        // FirefoxSafari 5
        success = document.execCommand('copy', false, null);
      } catch(e) {
        success = false;
      }
  
      // 
      document.body.removeChild(forExecElement);
    }
  
    if (success) {
      if (params && params.onSuccess) params.onSuccess(params.successMsg || (window._seeds_lang['copied_to_the_clipboard'] || ''))
    } else {
      if (params && params.onError) params.onError(params.errorMsg || (window._seeds_lang['unable_to_access_clipboard'] || ''))
    }
  }
}

export default Clipboard



