if (!window._seeds_lang) window._seeds_lang = {} // 
/* -------------------

------------------- */
Math.Calc = (function () {
  /* -----------------------------------------------------
   3.14 >> 314 100
  @param (argNum: {number} , argDigits: {number} , round: {bool}  true)
  @return result {times:100, num: 314}
  ----------------------------------------------------- */
  function toInteger (argNum, argDigits, round) {
    var result = { times: 1, num: 0 }
    var argNumStr = argNum + ''
    if (argNumStr.indexOf('.') === -1) {
      result.num = argNum
      return result
    }
    var digits = isNaN(argDigits) ? argNumStr.substr(argNumStr.indexOf('.') + 1).length : argDigits // 
    var times = Math.pow(10, digits)
    var num = parseInt(Math.abs(argNum) * times + (round === false ? 0 : 0.5), 10) // ,
    var isNegative = argNum < 0 // 
    result.times = times
    result.num = isNegative ? -num : num
    return result
  }

  /* -----------------------------------------------------
  
  

  @param a {number} 1
  @param b {number} 2
  @param digits {number}  2, 
  @param op {string} add/subtract/multiply/divide
  ----------------------------------------------------- */
  function operation (a, b, digits, op) {
    var o1 = toInteger(a)
    var o2 = toInteger(b)
    var n1 = o1.num
    var n2 = o2.num
    var t1 = o1.times
    var t2 = o2.times
    var max = t1 > t2 ? t1 : t2
    var result = null
    switch (op) {
      case 'add':
        if (t1 === t2) { // 
          result = Number(n1) + Number(n2)
        } else if (t1 > t2) { // o1   o2
          result = Number(n1) + Number(n2 * (t1 / t2))
        } else { // o1   o2
          result = Number(n1 * (t2 / t1)) + Number(n2)
        }
        result = result / max
        break
      case 'subtract':
        if (t1 === t2) {
          result = n1 - n2
        } else if (t1 > t2) {
          result = n1 - n2 * (t1 / t2)
        } else {
          result = n1 * (t2 / t1) - n2
        }
        result = result / max
        break
      case 'multiply':
        result = (n1 * n2) / (t1 * t2)
        break
      case 'divide':
        result = (n1 / n2) * (t2 / t1)
        break
      default:
        result = 0
    }
    if (digits && !isNaN(digits)) {
      // 
      return result.toFixed(digits)
    }
    return result
  }

  // 
  function add (a, b, digits) {
    return operation(a, b, digits, 'add')
  }
  function subtract (a, b, digits) {
    return operation(a, b, digits, 'subtract')
  }
  function multiply (a, b, digits) {
    return operation(a, b, digits, 'multiply')
  }
  function divide (a, b, digits) {
    return operation(a, b, digits, 'divide')
  }
  // toFixed 0.07.toFixed(1) => 0.0
  function toFixed (argNum, argDigits, fixed, round) {
    var result = toInteger(argNum, argDigits, round)
    if (fixed) return (result.num / result.times).toFixed(argDigits)
    return result.num / result.times
  }
  // 
  function toThousandth (num) {
    if (!num) return '0'
    var sExpr = /^(\d+)((\.\d+)?)$/
    var n1 = num.toString().replace(sExpr, function (s, s1, s2) {
      var reExpr = /\d{1,3}(?=(\d{3})+$)/g
      return s1.replace(reExpr, '$&,') + s2
    })
    return n1
  }
  // , , 
  function correctNumber (argNumstr, options) {
    const {max, min, digits, maxLength, required, onError} = options
    if (argNumstr === '' || isNaN(argNumstr) || min - max >= 0) {
      // 
      if (required) return min ? '' + min : '0'
      return ''
    }
    var value = String(argNumstr || '')
    // 
    if (!isNaN(max) && value - max > 0) {
      // callback onError
      if (onError) onError({errMsg: window._seeds_lang['hint_cannot_be_greater_than'] || '' + max})
      return '' + max
    }
    // 
    if (!isNaN(min) && value - min < 0) {
      // callback onError
      if (onError) onError({errMsg: window._seeds_lang['hint_cannot_be_less_than'] || '' + min})
      return '' + min
    }
    // 
    if (!isNaN(digits)) {
      console.log(digits)
      if (value.indexOf('.') !== -1 && digits - 0 >= 0 && digits.toString().indexOf('.') === -1) {
        if (digits - 0 === 0) { // 
          value = value.substring(0, value.indexOf('.'))
        } else { // 
          value = value.substring(0, value.indexOf('.') + Number(digits) + 1)
        }
      }
    }
    // 
    if (maxLength && value && value.length > maxLength) {
      value = value.substring(0, maxLength)
    }
    // Number, 001, , 1.0001
    if (value.indexOf('.') === -1) {
      return '' + Number(value)
    }
    return '' + value
  }
  // , 
  function correctPhone (argPhone, options) {
    const {onError} = options;
    let value = argPhone;
    // 
    if (isNaN(value)) {
      const result = value.match(/[0-9]{1,}/);
      if (result) value = result[0];
      else value = '';
      // callback onError
      if (onError) onError({errMsg: window._seeds_lang['hint_must_number'] || ''});
    }
    return value;
  }
  // exports
  return {
    add: add, // 1.22+12.22
    subtract: subtract, // 1.22-10.22
    multiply: multiply,
    divide: divide,
    toFixed: toFixed,
    toThousandth: toThousandth,
    correctNumber: correctNumber,
    correctPhone: correctPhone
  }
})();



