/**
* @overview
* @param {Point} :[latitude, longitude]
* @param {Polygon} :[[latitude, longitude], [latitude, longitude]]
* @param {Line} :[latitude1, longitude1, latitude2, longitude2]
* @param {Line<Array>} :[[latitude1, longitude1, latitude2, longitude2]]
*/

var GeoUtil = {};
(function () {
  /**
    * 
    * @param {Polygon} polygon 
    * @param {Boolean} isRegular , true, 
    * @return {Line<Array>}
    */
   GeoUtil.polygonToLines = function (polygon, isRegular) {
    var lines = []
    // 
    for (var i = 0; i < polygon.length - 1; i++) {
      lines.push([
        polygon[i][0], polygon[i][1],
        polygon[i + 1][0], polygon[i + 1][1]
      ])
    }
    // , , 
    if (isRegular) {
      lines.push([
        polygon[polygon.length - 1][0], polygon[polygon.length - 1][1],
        polygon[0][0], polygon[0][1]
      ])
    }
    return lines
  }

  /**
    * 
    * @param {Point} point 
    * @param {Polygon} polygon 
    * @return {Boolean}
    */
  GeoUtil.pointInsidePolygon = function (point, polygon) {
    var x = point[0], y = point[1]

    var inside = false
    for (var i = 0, j = polygon.length - 1; i < polygon.length; j = i++) {
      var xi = polygon[i][0], yi = polygon[i][1]
      var xj = polygon[j][0], yj = polygon[j][1]

      var intersect = ((yi > y) !== (yj > y))
        && (x < (xj - xi) * (y - yi) / (yj - yi) + xi)
      if (intersect) inside = !inside
    }

    return inside
  }
  /**
    * 
    * @param {Line} line0 
    * @param {Line} line1 
    * @return {Boolean}
    */
  GeoUtil.lineIntersectLine = function (line0, line1) {
    var [a,b,c,d] = line0
    var [p,q,r,s] = line1
    var det, gamma, lambda;
    det = (c - a) * (s - q) - (r - p) * (d - b);
    if (det === 0) {
      return false;
    } else {
      lambda = ((s - q) * (r - a) + (p - r) * (s - b)) / det;
      gamma = ((b - d) * (r - a) + (c - a) * (s - b)) / det;
      return (0 < lambda && lambda < 1) && (0 < gamma && gamma < 1);
    }
  }
  /**
    * 
    * @param {Polygon} py0 
    * @param {Polygon} py1 
    * @return {Boolean}
    */
  GeoUtil.polygonInsidePolygon = function (py0, py1) {
    // 
    var i;
    var inside = 0;
    for (i = 0; i < py0.length; i += 1) {
      if (GeoUtil.pointInsidePolygon(py0[i], py1)) {
        inside++;
      }
    }
    if (inside === py0.length) return true

    inside = 0;
    for (i = 0; i < py1.length; i += 1) {
      if (GeoUtil.pointInsidePolygon(py1[i], py0)) {
        inside++;
      }
    }
    if (inside === py0.length) return true

    return false
  }

  /**
    * 
    * @param {Point} point 
    * @return {Boolean}
    */
  GeoUtil.isPoint = function (point) {
    var precision = 2e-10
    if ((Math.abs(point[0]) < precision) && ((Math.abs(point[1]) < precision))) {
      return false
    } else {
      return true
    }
  }

  /**
    * 
    * @param {Point} p0
    * @param {Point} p1
    * @return {Boolean}
    */
  GeoUtil.equalPoint = function (p0, p1) {
    var precision = 2e-8
    if ((Math.abs(p0[0] - p1[0]) < precision) && ((Math.abs(p0[1] - p1[1]) < precision))) {
      return true
    } else {
      return false
    }
  }

  /**
    * 
    * @param {Point} p0
    * @param {Point} p1
    * @return {Point}
    */
  GeoUtil.getMiddlePoint = function (p0, p1) {
    return [(p0[0] + p0[1]) / 2, (p1[0] + p1[1]) / 2]
  }

  /**
    * , , , 
    * @param {Polygon} polygon
    * @returns {Boolean}
    */
  GeoUtil.isRegularPolygon = function (polygon) {
    var lines = GeoUtil.polygonToLines(polygon, true)
    for (var i = 0; i < lines.length; i++) {
      for (var j = i + 1; j < lines.length; j++) {
        if (GeoUtil.lineIntersectLine(lines[i], lines[j])) return true
      }
    }
    return false
  }

  /**
    * 
    * @param {Line} line
    * @param {Point} point
    * @return {Boolean}
    */
  GeoUtil.pointOnLine = function (point, line) {
    // 
    if (point[1] >= Math.min(line[1], line[3]) && point[1] <= Math.max(line[1], line[3]) && point[0] >= Math.min(line[0], line[2]) && point[0] <= Math.max(line[0], line[2])) {
      // 
      var precision = (line[1] - point[1]) * (line[2] - point[0]) - (line[3] - point[1]) * (line[0] - point[0])
      if (precision < 2e-10 && precision > -2e-10) { // 0
        return true
      }
    }
    return false
  }
  /* -----------------------------------------------------
    
    @params {point1: {longitude: '', latitude: ''}, point2: {longitude: '', latitude: ''}, fail: fn
    @return km
  ----------------------------------------------------- */
  /**
    * 
    * @param {Point} p0
    * @param {Point} p1
    * @return {Number} -1
    */
  GeoUtil.getDistance = function (p0, p1) {
    if (!p0[0] || !p0[1] || !p1[0] || !p1[1]) {
      return -1
    }
    var lat1 = p0[0]
    var lng1 = p0[1]
    var lat2 = p1[0]
    var lng2 = p1[1]
    var radLat1 = lat1 * Math.PI / 180.0
    var radLat2 = lat2 * Math.PI / 180.0
    var a = radLat1 - radLat2
    var b = lng1 * Math.PI / 180.0 - lng2 * Math.PI / 180.0
    var s = 2 * Math.asin(Math.sqrt(Math.pow(Math.sin(a / 2), 2) + Math.cos(radLat1) * Math.cos(radLat2) * Math.pow(Math.sin(b / 2), 2)))
    s = s * 6378.137 // EARTH_RADIUS;
    s = Math.round(s * 10000) / 10000
    return s
  }
})();

export default GeoUtil



