  // 
  // @params {urls:'http',index:'',layerHTML:''}
  preview: null,
  previewImage: function (params = {}) {
    if (!params.urls || !params.urls.length) {
      Bridge.showToast(`urls${window._seeds_lang['wrong_parameter'] || ''}, ${window._seeds_lang['cannot_preview'] || ''}`, {mask: false})
      return
    }
    var src = params.urls[params.index || 0]
    if (!src) {
      Bridge.showToast(`index${window._seeds_lang['wrong_parameter'] || ''}, ${window._seeds_lang['cannot_preview'] || ''}`, {mask: false})
      return
    }
    var layerHTML = params.layerHTML || ''
    if (!this.preview) {
      this.preview = new Preview({
        src: src,
        layerHTML: layerHTML,
        onSuccess: function (s) {
          s.show()
        },
        onError: function () {
          Bridge.showToast(`${window._seeds_lang['invalid_image_src'] || ''}`, {mask: false})
        }
      })
    } else {
      this.preview.setSrc(src)
      this.preview.setLayerHTML(layerHTML)
      this.preview.update()
    }
  },
