// MediaUtil 
var MediaUtil = {
  /* ---------------------------
    Methods
    --------------------------- */
  audio: function (src) {
    return new Audio(src)
  },
  video: function (src) {
    if (!src) return null
    var video = document.createElement('video')
    video.controls = true
    var source = document.createElement('source')
    source.type = this.sourceType(src)
    source.src = src
    video.appendChild(source)
    return video
  },
  getFileURL: function (file) { // , file = input.files[0]
    var url = null;
    if (window.createObjcectURL !== undefined) {
      url = window.createOjcectURL(file)
    } else if (window.URL !== undefined) {
      url = window.URL.createObjectURL(file)
    } else if (window.webkitURL !== undefined) {
      url = window.webkitURL.createObjectURL(file)
    }
    return url
  },
  getFileSize: function (file) { // 
    return file.size
  },
  getFileType: function (file) { // 
    return file.type
  },
  getFileBase64: function (file, callback) { // base64
    var reader = new FileReader()
    reader.onload = (e) => {
      callback(e.target.result)
    }
    reader.readAsDataURL(file)
  },
  convertTime: function (sec) { // 
    if (sec < 0) sec = -sec
    let sec_num = parseInt(sec, 10)
    let hours = Math.floor(sec_num / 3600)
    let minutes = Math.floor((sec_num - hours * 3600) / 60)
    let seconds = sec_num - hours * 3600 - minutes * 60
    if (hours < 10) {
      hours = '0' + hours
    }
    if (minutes < 10) {
      minutes = '0' + minutes
    }
    if (seconds < 10) {
      seconds = '0' + seconds
    }
    return minutes + ':' + seconds
  },
  convertSize: function (bytes) { // KBMB, *1000/1000
    if (isNaN(bytes)) return 0
    if (bytes > 1024 * 1024) return (Math.round(bytes * 1000 / (1024 * 1024)) / 1000).toString() + 'MB'
    return (Math.round(bytes * 1000 / 1024) / 1000).toString() + 'KB'
  },
  play: function (target) {
    target.play()
  },
  pause: function (target) {
    target.pause()
  },
  resume: function (target) {
    if (target.paused) {
      target.play()
    } else {
      target.pause()
    }
  },
  loop: function (target, loop) {
    target.loop = loop || false
  },
  autoplay: function (target, autoplay) {
    target.autoplay = autoplay || false
  },
  isReady: function (target) { // 0. 1. 2. 3.
    if (target.readyState !== 4) {
      return false
    }
    return true
  },
  toggleFullScreen: function (target) { // audio, 
    if (target.requestFullscreen) {
      target.exitFullscreen()
    } else {
      target.requestFullscreen()
    }
  },
  duration: function (target) {
    if (!this.isReady(target)) return
    return target.duration
  },
  currentTime: function (target, time) {
    if (!this.isReady(target)) return
    if (!isNaN(time)) {
      target.currentTime = time
    }
    return time
  },
  volume: function (target, volume) {
    if (!isNaN(volume)) {
      target.volume = volume
    }
    return target.volume
  },
  volumeLvl: function (target) {
    var volnumber = target.volume
    if (volnumber === 0) {
      return 0
    } else if (volnumber > 0 && volnumber < 0.3) {
      return 1
    } else if (volnumber > 0.3 && volnumber < 0.6) {
      return 2
    } else if (volnumber > 0.6 && volnumber < 0.9) {
      return 3
    } else {
      return 4
    }
  },
  rate: function (target, rate) { // 1.0
    if (!isNaN(rate)) {
      target.defaultPlaybackRate = rate
    }
    return target.defaultPlaybackRate
  },
  suffixTypes: function (suffix) {
    var type = ''
    var codeType = ''
    switch (suffix) {
      // 
      case 'aac':
        type = 'audio/mp4'
        codeType = 'audio/mp4; codecs="mp4a.40.2"'
        break
      case 'vorbis': // ogg
        type = 'audio/ogg'
        codeType = 'audio/ogg; codecs="vorbis"'
        break
      case 'wav':
        type = 'audio/wav'
        codeType = 'audio/wav; codecs="1"'
        break
      // 
      case 'h.264': // mpg4mp4mov
        type = 'video/mp4'
        codeType = 'video/mp4; codecs="avc1.42E01E, mp4a.40.2"'
        break
      case 'mp4':
        type = 'video/mp4'
        codeType = 'video/mp4; codecs="avc1.42E01E, mp4a.40.2"'
        break
      case 'theora': // ogg
        type = 'video/ogg'
        codeType = 'video/ogg; codecs="theora"'
        break
      case 'ogg':
        type = 'video/ogg'
        codeType = 'video/ogg; codecs="theora"'
        break
      case 'webm': // webm
        type = 'video/webm'
        codeType = 'video/webm; codecs="vp8, vorbis"'
        break
      default:
        type = ''
        codeType = ''
    }
    return [type, codeType]
  },
  isSupport: function (suffix) { // 
    var types = this.suffixTypes(suffix)
    var type = types[0]
    var codeType = types[1]
    var player = document.createElement('video')
    if (type !== '' && codeType !== '' && (player.canPlayType(type) || player.canPlayType(codeType))) {
      return true
    }
    return false
  },
  sourceType: function (src) {
    if (!src) return src
    return this.suffixTypes(src.substring(src.lastIndexOf('.') + 1, src.length).toLowerCase())[0]
  },
  /* ---------------------------
    Events
    --------------------------- */
  // dataunavailable // readyState0
  // canshowcurrentframe // readyState1
  // canplay // readyState2
  // canplaythrough // readyState3
  // load // loadcanplaythrough
  // loadeddata // 
  // loadedmetadata // 
  // loadstart // 
  // progress // 
  // abort // 
  // stalled // 
  // error // 
  // emptied // 
  // empty // 
  // play // 
  // playing // 
  // timeupdate // 
  // pause // 
  // waiting // 
  // ended // 
  // volumechange // 
  // ratechange // 
  // seeked // 
  // seeking // 
}

export default MediaUtil



