// require [PrototypeString.js] clearProtocol()
// ImgLazy 
import DB from './../DB'

var ImgLazy = function (params) {
  /* --------------------
  Model
  -------------------- */
  var defaults = {
    overflowContainer: document.body,
    load: 'scroll', // scroll  | queue  | all 
    threshold: 300, // 
    loadAttr: 'data-load-src', // 
    errorAttr: 'data-error-src', // 
    completeAttr: 'data-complete', // , data-complete=0, =1
  }
  params = params || {}
  for (var def in defaults) {
    if (params[def] === undefined) {
      params[def] = defaults[def]
    }
  }
  var s = this
  s.params = params
  // Container
  s.overflowContainer = typeof s.params.overflowContainer === 'string' ? document.querySelector(s.params.overflowContainer) : s.params.overflowContainer
  if (!s.overflowContainer) {
    console.log('SeedsUI Error : Dragrefresh overflowContainer')
    return
  }
  // 
  s.imgs = []
  // 
  s.scrollTop = 0
  /* --------------------
  Method
  -------------------- */
  // 
  s.getOffsetTop = function (el) {
    var offsetTop = el.offsetTop
    if (el.offsetParent != null) offsetTop += s.getOffsetTop(el.offsetParent)

    return offsetTop
  }
  // 
  s.isInScreen = function (el) {
    var offsetTop = s.getOffsetTop(el)
    if (offsetTop > s.scrollTop - s.params.threshold && offsetTop < parseInt(s.scrollTop, 10) + parseInt(window.innerHeight, 10)) {
      return true
    }
    return false
  }
  // 
  s.completeList = []
  s.updateCompleteList = function (src) {
    s.completeList = DB.getSession('seedsui_imglazy_complete_list') || []
    if (src && typeof src === 'string') {
      s.completeList.push(src.clearProtocol())
      DB.setSession('seedsui_imglazy_complete_list', s.completeList)
    }
  }
  s.updateCompleteList()
  // 
  s.isComplete = function (src) {
    if (src && typeof src === 'string' && s.completeList.indexOf(src.clearProtocol()) !== -1) {
      return true
    }
    return false
  }
  /* --------------------
  Events
  -------------------- */
  s.events = function (detach) {
    var action = detach ? 'removeEventListener' : 'addEventListener'
    if (s.params.load === 'scroll') {
      var scrollTarget = s.overflowContainer === document.body ? window : s.overflowContainer
      scrollTarget[action]('scroll', s.onScroll, false)
    }
  }
  s.attach = function () {
    s.events()
  }
  s.detach = function () {
    s.events(false)
  }
  // 
  s.onLoad = function (e) {
    var target = e.target
    var imgTarget = target.$el
    imgTarget.setAttribute(s.params.completeAttr, '1')
    // seesion
    s.updateCompleteList(target.src)
    
    // 
    s.render(imgTarget, target.src)
    // console.log('' + target.src)
  }
  // 
  s.render = function (target, src) {
    if (target.tagName === 'IMG') {
      target.src = src
    } else {
      target.style.backgroundImage = 'url(' + src + ')'
    }
  }
  // 
  s.onError = function (e) {
    var target = e.target
    var imgTarget = target.$el
    imgTarget.setAttribute(s.params.completeAttr, '0')
    if (target.errorSrc) {
      // 
      s.render(imgTarget, target.errorSrc)
    }
    // console.log('' + target.src)
  }
  //  (300,onScrollEnd)
  var timer
  var millisec = 300
  s.getScrollTop = function () {
    return s.overflowContainer === document.body ? document.documentElement.scrollTop : s.overflowContainer.scrollTop
  }
  s.onScroll = function () {
    s.scrollTop = s.getScrollTop()
    if (timer) {
      clearTimeout(timer)
    }
    timer = setTimeout(function () {
      if (s.scrollTop === s.getScrollTop()) {
        s.onScrollEnd()
        clearTimeout(timer)
        return
      }
      timer = null
    }, millisec)
  }
  s.onScrollEnd = function () {
    console.log('')
    s.load()
  }
  /* --------------------
  Init
  -------------------- */
  s.load = function () {
    // 
    s.updateCompleteList()

    // 
    s.imgs = [].slice.call(s.overflowContainer.querySelectorAll('[' + s.params.loadAttr + ']')).filter((n) => {
      if (n.getAttribute(s.params.loadAttr)) return true
      return false
    })

    // 
    if (s.params.load === 'queue') {
      s.queue(0)
      return
    }

    // 
    for (var i = 0; i < s.imgs.length; i++) {
      // 
      var loadSrc = s.imgs[i].getAttribute(s.params.loadAttr)
      var errorSrc = s.imgs[i].getAttribute(s.params.imgErrorAttr) || ''
      // 
      if (s.isComplete(loadSrc)) {
        s.render(s.imgs[i], loadSrc) // 
        continue
      }
      
      var flag = true
      if (s.params.load === 'scroll') flag = s.isInScreen(s.imgs[i]) // 
      if (flag && !s.imgs[i].getAttribute(s.params.completeAttr)) {
        console.log('' + loadSrc)
        var image = new Image()
        image.$el = s.imgs[i]
        image.src = loadSrc
        image.errorSrc = errorSrc
        image.addEventListener('load', s.onLoad, false)
        image.addEventListener('error', s.onError, false)
      }
    }
  }
  // 
  s.queue = function (i) {
    // 
    var loadSrc = s.imgs[i].getAttribute(s.params.loadAttr)
    var errorSrc = s.imgs[i].getAttribute(s.params.imgErrorAttr) || ''
    // 
    if (s.isComplete(loadSrc)) {
      s.render(s.imgs[i], loadSrc) // 
      s.queue(i++)
      return
    }
    // 
    if (s.imgs[i].getAttribute(s.params.completeAttr)) {
      i++
    }
    if (!s.imgs[i]) {
      return
    }
    
    var image = new Image()
    image.$el = s.imgs[i]
    image.src = loadSrc
    image.errorSrc = errorSrc
    image.addEventListener('load', function (e) {
      console.log('' + i + '')
      s.onLoad(e)
      s.queue(i++)
    }, false)
    image.addEventListener('error', function (e) {
      console.log('' + i + '')
      s.onError(e)
      s.queue(i++)
    }, false)
  }
  s.update = function () {
    if (s.params.load === 'scroll') {
      s.detach()
      s.attach()
    }
  }
  s.update()
}

export default ImgLazy



